@font-face {
    font-family: 'strange_times';
    src: url('/assets/fonts/strangetimes-regular-webfont.woff2') format('woff2'),
         url('/assets/fonts/strangetimes-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

:root {
    --font-stack-heading: 'strange_times', Georgia, 'Times New Roman', Times, serif;

    --color-1: #fff;
}

img, figure {
    margin-left: auto;
    margin-right: auto;
}

header {
    margin-top: 2em;

    img {
        max-width: 45em;
        margin-bottom: 2em;
    }
    
    animation: fadeIn;
    animation-duration: 1s;
}

header p {
    font-style: italic;
}

h1 {
    text-transform: none;
    font-size: 3em;
}